.education {
    width: 100%;
    height: 100%;
    background-color: black;
    padding-bottom: 450px;
    padding-top: 10px;
}

#education {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 12%;
    padding-top: 15%;
    padding-bottom: 10%;
}

.education-title {
    font-size: 60px;
    font-weight: bold;
    color: white;
    align-items: center;
    text-decoration: underline;
    padding-left: 3%;
    padding-top: 2px;
    padding-bottom: 2px;
}

.university-title {
    color: white;
    font-size: 30px;
    font-weight: bold;
    padding-left:3%;
}

.education-date {
    color: white;
    font-size: 25px;
    padding-bottom: 5px;
    padding-left: 3%;
}

ul.education-description {
    list-style-type: disc;
    color: white;
    font-size: 25px;
    padding-left: 5%;
}

li.elem{
    width: 100%;
    padding-left: 5px;
}


@media screen and (max-width: 720px) {
    .education-title {
        font-size: 30px;
    }
    
    .university-title {
        color: white;
        font-size: 13px;
        font-weight: bold;
    }
    
    .education-date {
        color: white;
        font-size: 12px;
        padding-bottom: 5px;
    }
    
    ul.education-description {
        list-style-type: disc;
        color: white;
        font-size: 12px;
        padding-left: 8%;
        padding-right: 9%;
    }

    li.elem{
        width: 100%;
        padding-left: 5px;
    }
    
}